body {
  background-color: #707070;
}

#title {
  font-size: 40px;
  width: 100%;
  text-align: center;
  font-family: "Ubuntu", sans-serif;
  color: #ffffff;
}

#loginDiv {
  font-size: 30px;
  width: 100%;
  align-self: center;
  align-content: center;
  align-items: center;
  text-align: center;
  padding: 0px;
  background-color: #b2b2b2;
}

#loggedInDiv {
  font-size: 30px;
  width: 70%;
  margin-left: 15%;
  padding: 25px;
  background-color: #b2b2b2;
}

#accessUIDiv {
  font-size: 30px;
  width: 70%;
  margin-left: 15%;
  padding: 25px;
  background-color: #b2b2b2;
}

.buttons {
  font-size: 30px;
  border-radius: 25px;
  width: 50%;
  margin-left: 25%;
  font-family: "Ubuntu", sans-serif;
}

.buttons:hover {
  background-color: #00d5124f;
  color: #ffffff;
  opacity: 50%;
}

#loginName {
  font-size: 30px;
}
#loginPassword {
  font-size: 30px;
}
#searchText {
  font-size: 30px;
}
#colorText {
  font-size: 30px;
}

input[type="text"],
input[type="password"] {
  border-radius: 25px;
  text-align: center;
  width: 80%;
  margin-left: 10%;
  margin-bottom: 2%;
  font-family: "Ubuntu", sans-serif;
}

#inner-title {
  display: inline-block;
  font-size: 0.8em;
  width: 90%;
  text-align: center;
  margin-left: 5%;
  margin-bottom: 2%;
  font-family: "Ubuntu", sans-serif;
  color: #95060a;
}

#loginResult {
  display: inline-block;
  font-size: 0.8em;
  width: 90%;
  text-align: center;
  margin-left: 5%;
  margin-bottom: 2%;
  font-family: "Ubuntu", sans-serif;
  color: #95060a;
}

#userName {
  display: inline-block;
  width: 80%;
  margin-left: 10%;
  text-align: center;
}

#logoutButton {
  width: 20%;
  margin-left: 40%;
}

select {
  width: 30%;
  margin-left: 35%;
  margin-top: 2%;
  font-size: 0.75em;
  font-family: "Ubuntu", sans-serif;
}

#colorSearchResult {
  display: inline-block;
  font-size: 0.8em;
  width: 90%;
  text-align: center;
  margin-left: 5%;
  margin-top: 2%;
  margin-bottom: 1%;
  font-family: "Ubuntu", sans-serif;
  color: #95060a;
}

#colorAddResult {
  display: inline-block;
  font-size: 0.8em;
  width: 90%;
  text-align: center;
  margin-left: 5%;
  margin-top: 2%;
  margin-bottom: 1%;
  font-family: "Ubuntu", sans-serif;
  color: #95060a;
}
